	module.exports = {"pathContext":{"docHtml":"<h1>forwarded</h1>\n<p><a href=\"https://npmjs.org/package/forwarded\"><img src=\"https://img.shields.io/npm/v/forwarded.svg\" alt=\"NPM Version\"></a>\n<a href=\"https://npmjs.org/package/forwarded\"><img src=\"https://img.shields.io/npm/dm/forwarded.svg\" alt=\"NPM Downloads\"></a>\n<a href=\"https://nodejs.org/en/download/\"><img src=\"https://img.shields.io/node/v/forwarded.svg\" alt=\"Node.js Version\"></a>\n<a href=\"https://travis-ci.org/jshttp/forwarded\"><img src=\"https://img.shields.io/travis/jshttp/forwarded/master.svg\" alt=\"Build Status\"></a>\n<a href=\"https://coveralls.io/r/jshttp/forwarded?branch=master\"><img src=\"https://img.shields.io/coveralls/jshttp/forwarded/master.svg\" alt=\"Test Coverage\"></a></p>\n<p>Parse HTTP X-Forwarded-For header</p>\n<h2>Installation</h2>\n<p>This is a <a href=\"https://nodejs.org/en/\">Node.js</a> module available through the\n<a href=\"https://www.npmjs.com/\">npm registry</a>. Installation is done using the\n<a href=\"https://docs.npmjs.com/getting-started/installing-npm-packages-locally\"><code>npm install</code> command</a>:</p>\n<pre><code class=\"language-sh\">$ npm install forwarded\n</code></pre>\n<h2>API</h2>\n<pre><code class=\"language-js\">var forwarded = require('forwarded')\n</code></pre>\n<h3>forwarded(req)</h3>\n<pre><code class=\"language-js\">var addresses = forwarded(req)\n</code></pre>\n<p>Parse the <code>X-Forwarded-For</code> header from the request. Returns an array\nof the addresses, including the socket address for the <code>req</code>, in reverse\norder (i.e. index <code>0</code> is the socket address and the last index is the\nfurthest address, typically the end-user).</p>\n<h2>Testing</h2>\n<pre><code class=\"language-sh\">$ npm test\n</code></pre>\n<h2>License</h2>\n<p><a href=\"LICENSE\">MIT</a></p>","docPath":"argo-ci/node_modules/forwarded/readme","proj":"argo-ci"}}

